  <div class="calculator-container">
    <h2>Ìè¨Í∑∏Î¶¨Ìä∏ ÏãùÍµ¨Îì§ÏùÑ ÏúÑÌïú Í∑ºÎ¨¥ÏãúÍ∞Ñ Í≥ÑÏÇ∞Í∏∞</h2>
    
    <div class="input-group">
