      <div class="time-input">
        <input type="number" v-model="targetHours" min="0"> ÏãúÍ∞Ñ
        <input type="number" v-model="targetMinutes" min="0" max="59"> Î∂Ñ
      </div>
    </div>

    <div class="input-group">
      <h3>ÏÇ∞Ï†ï Í∏∞Í∞Ñ ÎÇ¥ ÎàÑÏ†Å Í∑ºÎ¨¥ÏãúÍ∞Ñ</h3>
      <div class="time-input">
        <input type="number" v-model="workedHours" min="0"> ÏãúÍ∞Ñ
        <input type="number" v-model="workedMinutes" min="0" max="59"> Î∂Ñ
      </div>
    </div>

    <div class="input-group">
      <h3>Ïù¥ÎØ∏ Í≤∞Ïû¨Îêú ÏÇ¨Ïö© ÏòàÏ†ï Ïó∞Ï∞®Í∞Ä ÏûàÎäî Í≤ΩÏö∞ ÏïÑÎûòÏóê Íº≠ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî!</h3>
      <div class="leave-input">
        <div>
          <label>Ïó∞Ï∞®:</label>
          <input type="number" v-model="fullDayLeave" min="0"> Ïùº
        </div>
        <div>
          <label>Î∞òÏ∞®:</label>
          <input type="number" v-model="halfDayLeave" min="0"> Ìöå
        </div>
        <div>
          <label>Î∞òÎ∞òÏ∞®:</label>
          <input type="number" v-model="quarterDayLeave" min="0"> Ìöå
        </div>
      </div>
    </div>
    <div class="include-today" style="margin-bottom: 20px">
      <label>
        <input 
          type="checkbox" 
          v-model="includeToday"
        > Ïò§Îäò Ìè¨Ìï®ÌïòÏó¨ Í≥ÑÏÇ∞ÌïòÍ∏∞
      </label>
    </div>

    <button @click="calculateRequiredTime" class="calculate-btn">Í≥ÑÏÇ∞ÌïòÍ∏∞</button>

    <Transition name="flash">
      <div v-if="result" :key="animationKey" class="result">
        <h3>Í≤∞Í≥º</h3>
        <p v-html="result"></p>
      </div>
    </Transition>

    <!-- Íµ¨Î∂ÑÏÑ† Ï∂îÍ∞Ä -->
    <hr class="my-8 border-gray-200">
    
    <!-- ÏóÖÎç∞Ïù¥Ìä∏ Í∏∞Î°ù ÏÑπÏÖò -->
    <div class="update-history mt-8 p-4 bg-gray-100 rounded-lg">
      <h3 class="text-lg font-semibold mb-4">ÏóÖÎç∞Ïù¥Ìä∏ Í∏∞Î°ù</h3>
      <ul class="list-disc pl-5 space-y-2">
        <li>2024.12.26 - Ï¥àÍ∏∞ Î≤ÑÏ†Ñ Ï∂úÏãú</li>
        <li>2024.12.27 - 'Ïó∞Ï∞® Ï†úÏô∏ÌïòÍ∏∞' Í∏∞Îä• Ïò§Î•ò ÏàòÏ†ï + Ïò§Îäò ÎÇ†ÏßúÎ•º UTCÍ∞Ä ÏïÑÎãå KST Í∏∞Ï§ÄÏúºÎ°ú ÏàòÏ†ï</li>
        <li>2025.01.08 - ÏûÑÏãú Í≥µÌú¥Ïùº ÏßÄÏ†ïÏúºÎ°ú Ïù∏Ìïú, Í≥µÌú¥Ïùº Î™©Î°ù ÏàòÏ†ï</li>
        <li>2025.01.24 - ÏïàÎÇ¥ Î¨∏Íµ¨ Î≥ÄÍ≤Ω/ Ïó∞Ï∞®,Î∞òÏ∞®,Î∞òÎ∞òÏ∞® Í≥ÑÏÇ∞ Î∞©Ïãù Î≥ÄÍ≤Ω/ Í∑ºÎ¨¥ Î™©Ìëú Îã¨ÏÑ± Ï°∞Í±¥ Î≥¥ÏôÑ/ Í≤∞Í≥ºÏ∞Ω ÍπúÎπ°ÏûÑ Ïï†ÎãàÎ©îÏù¥ÏÖò Ï∂îÍ∞Ä</li>
      </ul>
      
      <!-- Í≤ΩÍ≥† Î¨∏Íµ¨ Ï∂îÍ∞Ä -->
      <div class="mt-4 p-3 bg-yellow-50 border border-yellow-200 rounded text-sm text-yellow-800">
        ‚ö†Ô∏è Î≥∏ Í≥ÑÏÇ∞Í∏∞Ïùò Í≤∞Í≥ºÎäî Ï∞∏Í≥†Ïö©ÏúºÎ°úÎßå ÏÇ¨Ïö©Ìï¥ Ï£ºÏÑ∏Ïöî. ÌòπÏãú Î∂ÄÏ†ïÌôïÌïú Í≥ÑÏÇ∞ Í≤∞Í≥º Î∞úÍ≤¨ Ïãú, dohyeon.an@kakao.comÏúºÎ°ú Ïó∞ÎùΩ Î∂ÄÌÉÅÎìúÎ¶ΩÎãàÎã§ :)
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'WorkingHourCalculator',
  data() {
    return {
      targetHours: Number(localStorage.getItem('targetHours')) || 0,
      targetMinutes: Number(localStorage.getItem('targetMinutes')) || 0,
      workedHours: Number(localStorage.getItem('workedHours')) || 0,
      workedMinutes: Number(localStorage.getItem('workedMinutes')) || 0,
      fullDayLeave: Number(localStorage.getItem('fullDayLeave')) || 0,
      halfDayLeave: Number(localStorage.getItem('halfDayLeave')) || 0,
      quarterDayLeave: Number(localStorage.getItem('quarterDayLeave')) || 0,
      includeToday: true,
      result: '',
    }
  },
  watch: {
    // Í∞Å ÏûÖÎ†•Í∞íÏù¥ Î≥ÄÍ≤ΩÎê† ÎïåÎßàÎã§ localStorageÏóê Ï†ÄÏû•
    targetHours(newVal) {
      localStorage.setItem('targetHours', newVal)
    },
    targetMinutes(newVal) {
      localStorage.setItem('targetMinutes', newVal)
    },
    workedHours(newVal) {
      localStorage.setItem('workedHours', newVal)
    },
    workedMinutes(newVal) {
      localStorage.setItem('workedMinutes', newVal)
    },
    fullDayLeave(newVal) {
      localStorage.setItem('fullDayLeave', newVal)
    },
    halfDayLeave(newVal) {
      localStorage.setItem('halfDayLeave', newVal)
    },
    quarterDayLeave(newVal) {
      localStorage.setItem('quarterDayLeave', newVal)
    }
  },
  methods: {
    isHoliday(dateStr) {
      // 2024ÎÖÑ Í≥µÌú¥Ïùº Î™©Î°ù
      const holidays = [
        '2024-01-01', // Ïã†Ï†ï
        '2024-02-09', // ÏÑ§ÎÇ†
        '2024-02-10', // ÏÑ§ÎÇ†
        '2024-02-11', // ÏÑ§ÎÇ†
        '2024-02-12', // ÎåÄÏ≤¥Í≥µÌú¥Ïùº
        '2024-03-01', // ÏÇºÏùºÏ†à
        '2024-04-10', // Íµ≠ÌöåÏùòÏõêÏÑ†Í±∞
        '2024-05-05', // Ïñ¥Î¶∞Ïù¥ÎÇ†
        '2024-05-06', // ÎåÄÏ≤¥Í≥µÌú¥Ïùº
        '2024-05-15', // Î∂ÄÏ≤òÎãòÏò§Ïã†ÎÇ†
        '2024-06-06', // ÌòÑÏ∂©Ïùº
        '2024-08-15', // Í¥ëÎ≥µÏ†à
        '2024-09-16', // Ï∂îÏÑù
        '2024-09-17', // Ï∂îÏÑù
        '2024-09-18', // Ï∂îÏÑù
        '2024-10-03', // Í∞úÏ≤úÏ†à
        '2024-10-09', // ÌïúÍ∏ÄÎÇ†
        '2024-12-25', // ÏÑ±ÌÉÑÏ†à
        '2025-01-01', // Ïã†Ï†ï
        '2025-01-27', // ÏûÑÏãúÍ≥µÌú¥Ïùº
        '2025-01-28', // ÏÑ§ÎÇ†
        '2025-01-29', // ÏÑ§ÎÇ†
        '2025-01-30', // ÏÑ§ÎÇ†
        '2025-03-03', // ÎåÄÏ≤¥Ìú¥Ïùº
        '2025-05-05', // Ïñ¥Î¶∞Ïù¥ÎÇ†
        '2025-05-06', // ÎåÄÏ≤¥Ìú¥Ïùº
        '2025-06-06', // ÌòÑÏ∂©Ïùº
        '2025-08-15', // Í¥ëÎ≥µÏ†à
        '2025-10-03', // Í∞úÏ≤úÏ†à
        '2025-10-06', // Ï∂îÏÑù
        '2025-10-07', // Ï∂îÏÑù
        '2025-10-08', // Ï∂îÏÑù
        '2025-10-09', // ÌïúÍ∏ÄÎÇ†
        '2025-12-25', // ÏÑ±ÌÉÑÏ†à
      ]
      return holidays.includes(dateStr)
    },

    calculateRequiredTime() {
      this.animationKey += 1  // Í≥ÑÏÇ∞Ìï† ÎïåÎßàÎã§ key Í∞íÏùÑ Ï¶ùÍ∞ÄÏãúÌÇ¥
      
      // Î™©Ìëú ÏãúÍ∞ÑÏùÑ Î∂ÑÏúºÎ°ú Î≥ÄÌôò
      const targetMinutesTotal = (this.targetHours * 60) + Number(this.targetMinutes)
      
      // Ïã§Ï†ú Í∑ºÎ¨¥Ìïú ÏãúÍ∞ÑÏùÑ Î∂ÑÏúºÎ°ú Î≥ÄÌôò
      const workedMinutesTotal = (this.workedHours * 60) + Number(this.workedMinutes)
      
      // Ìú¥Í∞Ä ÏãúÍ∞Ñ Í≥ÑÏÇ∞ (8ÏãúÍ∞Ñ Í∑ºÎ¨¥ Í∏∞Ï§Ä)
      const leaveMinutes = (this.halfDayLeave * 240) + (this.quarterDayLeave * 120)
      const leaveDays = (this.fullDayLeave * 1)
      
      // ÌòÑÏû¨ ÎÇ†Ïßú ÏÑ§Ï†ï (ÌïúÍµ≠ ÏãúÍ∞Ñ)
      const today = new Date(new Date().toLocaleString('en-US', { timeZone: 'Asia/Seoul' }))
      const currentDate = new Date(today.getFullYear(), today.getMonth(), today.getDate())
        .toLocaleDateString('fr-CA')  // YYYY-MM-DD ÌòïÏãùÏúºÎ°ú Î∞òÌôò
      
      console.log(today)
      console.log(currentDate)
      
      // Ìï¥Îãπ Îã¨Ïùò Ï≤´Ïß∏ ÎÇ†Í≥º ÎßàÏßÄÎßâ ÎÇ† Í≥ÑÏÇ∞ (ÌïúÍµ≠ ÏãúÍ∞Ñ Í∏∞Ï§Ä)
      const firstDay = new Date(today.getFullYear(), today.getMonth(), 1)
      const lastDay = new Date(today.getFullYear(), today.getMonth() + 1, 0)

      // ÎÇ®ÏùÄ ÏòÅÏóÖÏùº Í≥ÑÏÇ∞
      let remainingWorkdays = 0 - leaveDays
      let isCounted = false
      let remainingMinutes = targetMinutesTotal - workedMinutesTotal + leaveMinutes

      for (let d = firstDay; d <= lastDay; d.setDate(d.getDate() + 1)) {
        const dateStr = new Date(d.getTime() + (9 * 60 * 60 * 1000))
          .toISOString()
          .split('T')[0]
        
        // ÌòÑÏû¨ ÎÇ†ÏßúÎ∂ÄÌÑ∞ Ïπ¥Ïö¥Ìä∏ ÏãúÏûë
        if (dateStr === currentDate) {
          isCounted = true
        }
        
        // ÌèâÏùºÏù∏ Í≤ΩÏö∞
        if (d.getDay() !== 0 && d.getDay() !== 6) {
          if (!this.isHoliday(dateStr) && isCounted) {
            // Í≥µÌú¥ÏùºÏù¥ ÏïÑÎãàÍ≥† ÌòÑÏû¨ ÎÇ†Ïßú Ïù¥ÌõÑÏù∏ Í≤ΩÏö∞ÏóêÎßå Ïπ¥Ïö¥Ìä∏
            remainingWorkdays++
          }
          
          if (this.isHoliday(dateStr) && isCounted) {
            // Í≥µÌú¥ÏùºÏù∏ Í≤ΩÏö∞ ÌïÑÏöî Í∑ºÎ¨¥ÏãúÍ∞ÑÏóêÏÑú ÌïòÎ£®Ïπò(480Î∂Ñ) Ï∞®Í∞ê
            remainingMinutes -= 480
          }
        }
      }
      
      console.log(remainingWorkdays)
      console.log(remainingMinutes)

      const actualWorkdays = this.includeToday ? remainingWorkdays : remainingWorkdays - 1

      if (actualWorkdays <= 0) {
        let message = ''
        if (remainingMinutes <= 0) {
          message = 'üéâ Î™©ÌëúÎ•º Ïù¥ÎØ∏ Îã¨ÏÑ±ÌïòÏÖ®ÏäµÎãàÎã§!'
        } else {
          const remainingHours = Math.floor(remainingMinutes / 60)
          const remainingMins = Math.round(remainingMinutes % 60)
          message = `‚ùå ÎÇ®ÏùÄ Í∑ºÎ¨¥ÏùºÏù¥ ÏóÜÏñ¥ Î™©Ìëú Îã¨ÏÑ±Ïù¥ Î∂àÍ∞ÄÎä•Ìï©ÎãàÎã§.\n(Î∂ÄÏ°±Ìïú Í∑ºÎ¨¥ ÏãúÍ∞Ñ: ${remainingHours}ÏãúÍ∞Ñ ${remainingMins}Î∂Ñ)`
        }
        this.result = ''
        setTimeout(() => {
          this.result = message
        }, 0)
        return
      }

      // ÌïòÎ£® ÌèâÍ∑† Í∑ºÎ¨¥Ìï¥Ïïº Ìï† ÏãúÍ∞Ñ Í≥ÑÏÇ∞
      const minutesPerDay = remainingMinutes / actualWorkdays
      const hoursPerDay = Math.floor(minutesPerDay / 60)
      const minutesRemainder = Math.round(minutesPerDay % 60)

      // ÎÇ®ÏùÄ Í≥µÌú¥Ïùº Ï∞æÍ∏∞
      const remainingHolidays = []
      for (let d = new Date(today); d <= lastDay; d.setDate(d.getDate() + 1)) {
        const dateStr = new Date(d.getTime() + (9 * 60 * 60 * 1000))
          .toISOString()
          .split('T')[0]
        if (this.isHoliday(dateStr)) {
          const holidayDate = new Date(dateStr)
          const month = holidayDate.getMonth() + 1
          const day = holidayDate.getDate()
          remainingHolidays.push(`${month}Ïõî ${day}Ïùº`)
        }
      }

      // Ïó∞Ï∞® Ï†ïÎ≥¥ Î¨∏ÏûêÏó¥ ÏÉùÏÑ±
      const leaveInfo = []
      if (this.fullDayLeave > 0) leaveInfo.push(`Ïó∞Ï∞® ${this.fullDayLeave}Ïùº`)
      if (this.halfDayLeave > 0) leaveInfo.push(`Î∞òÏ∞® ${this.halfDayLeave}Ìöå`)
      if (this.quarterDayLeave > 0) leaveInfo.push(`Î∞òÎ∞òÏ∞® ${this.quarterDayLeave}Ìöå`)
      
      const holidayText = remainingHolidays.length > 0 
        ? `\n\nÏù¥Î≤à Îã¨ ÎÇ®ÏùÄ Í≥µÌú¥Ïùº: ${remainingHolidays.join(',')}`
        : `\n\nÏù¥Î≤à Îã¨ ÎÇ®ÏùÄ Í≥µÌú¥ÏùºÏù¥ ÏóÜÏäµÎãàÎã§.`

      const includeTodayText = this.includeToday ? 'Ïò§ÎäòÏùÑ Ìè¨Ìï®ÌïòÏó¨' : ''

      const resultMessage = `${today.getFullYear()}ÎÖÑ ${today.getMonth() + 1}Ïõî Î™©Ìëú Îã¨ÏÑ±ÏùÑ ÏúÑÌï¥ \n${includeTodayText} ÎÇ®ÏùÄ <strong>${actualWorkdays} Í∑ºÎ¨¥Ïùº</strong> ÎèôÏïà\nÎß§Ïùº <strong>${hoursPerDay}ÏãúÍ∞Ñ ${minutesRemainder}Î∂Ñ</strong>Ïî© Í∑ºÎ¨¥Ìï¥Ïïº Ìï©ÎãàÎã§.${holidayText}`
      
      this.result = ''  // Í≤∞Í≥ºÎ•º Ïû†Ïãú ÎπÑÏõ†Îã§Í∞Ä
      setTimeout(() => {  // Îã§Ïùå Ìã±Ïóê Îã§Ïãú ÏÑ§Ï†ï
        this.result = resultMessage
      }, 0)
    }
  }
}
</script>

